/* FONT FACE */
.PublicBody {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
  @media (max-width: 759px) {
    .PublicBody {
      overflow-y: visible; } }

.PublicBody--grey {
  background: #F9F9F9; }

.PublicBody-content {
  display: flex;
  flex-grow: 1;
  margin-top: 120px; }

.PublicBody.PublicBody--grey .PublicBody-content {
  background: #F9F9F9; }
