/* FONT FACE */
.UserInfo {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding: 76px 0 28px 0;
  border: 1px solid #dddddd;
  border-right: none;
  border-left: none;
  background: #f9f9f9;
  text-align: center; }

.UserInfo-avatar {
  position: absolute;
  z-index: 10000001;
  top: -48px;
  left: 50%;
  margin-left: -48px; }

.UserInfo-name {
  margin-bottom: 4px;
  color: #333333;
  font-size: 22px;
  font-weight: 300;
  line-height: 24px; }

.UserInfo-nameBreadcrumb,
.UserInfo-nameBreadcrumb a {
  color: #999999; }
  .UserInfo-nameBreadcrumb:hover,
  .UserInfo-nameBreadcrumb a:hover {
    color: #666666; }

.UserInfo-desc {
  max-width: 620px;
  margin: 12px 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 22px; }

.UserInfo-links {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: #cccccc; }

.UserInfo-linksItem {
  display: flex;
  flex-wrap: nowrap;
  margin: 12px 0;
  line-height: 1; }

.UserInfo-linksItem .CDB-IconFont {
  margin-right: 4px; }

.UserInfo-linksItem--positive,
.UserInfo-linksItem--positive a {
  color: #8fb83f; }

@media (min-width: 760px) {
  .UserInfo-links {
    flex-direction: row; }
  .UserInfo-linksItem:not(:last-child) {
    margin-right: 28px; } }
