.FavMap {
  position: relative;
  height: 140px;
  overflow: hidden;
  background: #333 url("/assets/1.0.0-assets.136/images/layout/noise.png") repeat 0 0; }

.FavMap::before {
  content: '';
  position: absolute;
  z-index: 9995;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  transition: all 100ms ease-in-out;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1)); }

.FavMap:not(.is-pre-loading):not(.is-loading)::before {
  height: 20px; }

.FavMap-spinner {
  display: none;
  position: absolute;
  top: 34px;
  left: 50%;
  margin-left: -18px; }

.FavMap.is-pre-loading .FavMap-spinner,
.FavMap.is-loading .FavMap-spinner {
  display: block; }

.FavMap.FavMap--tall {
  height: 225px; }

.FavMap.FavMap--highShadow::before {
  height: 180px;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
  pointer-events: none; }

.FavMap.FavMap--highShadow:not(.is-pre-loading):not(.is-loading)::before {
  height: 150px; }

.FavMapTitle {
  position: absolute;
  z-index: 10000;
  bottom: 45px;
  width: 100%;
  transition: opacity 150ms ease-in-out;
  opacity: 1;
  line-height: normal; }

.FavMapTitle.is-hidden {
  opacity: 0; }

.FavMapTitle-inner {
  box-sizing: border-box;
  width: 940px;
  margin: auto; }

.FavMapTitle-sectionTitle,
.FavMapTitle-mapTitle {
  color: #FFF;
  text-decoration: none;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5); }

.FavMapTitle-sectionTitle {
  margin: 0 0 10px;
  font-size: 16px;
  font-weight: lighter;
  text-transform: uppercase; }

.FavMapTitle-mapTitle {
  margin: 0;
  font-size: 48px;
  font-weight: normal; }

.FavMapTitle-mapTitleLink {
  color: #FFF; }
  .FavMapTitle-mapTitleLink:hover {
    color: #FFF;
    text-decoration: none; }

@media (min-width: 760px) {
  .FavMap {
    height: 280px; }
  .FavMap-spinner {
    top: 104px; }
  .FavMap.FavMap--tall {
    height: 450px; }
  .FavMap--tall .FavMap-spinner {
    top: 189px; } }

@media only screen and (max-width: 320px) {
  .FavMap {
    height: 280px; }
  .FavMap-spinner {
    top: 104px; }
  .FavMap.FavMap--tall {
    height: 450px; }
  .FavMap--tall .FavMap-spinner {
    top: 189px; } }

@media only screen and (min-width: 320px) and (max-width: 955px) {
  .FavMap .FavMapTitle-inner {
    width: 100%;
    padding: 0 20px; } }

@media only screen and (min-width: 320px) and (max-width: 760px) {
  .FavMap {
    height: 320px; }
  .FavMap-spinner {
    top: 124px; }
  .FavMap.FavMap--tall {
    height: 320px; }
  .FavMap--tall .FavMap-spinner {
    top: 124px; } }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
  .FavMap {
    height: 300px; }
  .FavMap-spinner {
    top: 114px; }
  .FavMap.FavMap--tall {
    height: 300px; }
  .FavMap--tall .FavMap-spinner {
    top: 114px; } }
